import { Component, OnInit, Injector, ViewChild } from '@angular/core';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

import { ModalService } from 'src/app/shared/services/modal.service';

import * as _ from 'underscore';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MatDrawer } from '@angular/material/sidenav';
@Component({
  selector: 'app-water-report',
  templateUrl: './water-report.component.html',
  styleUrls: ['./water-report.component.scss']
})
export class WaterReportComponent implements OnInit {

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  isReportLoaded:boolean = false;

  filteredUtilityWaterListData: any;
  propertyTowerData: any;
  selectedTower: any;


  utilitySearch:string = "";

  utilityElecteicalListPeriods: any;

  apartment_id: number;
  utilityWaterListData:any;

  unitFieldType:string = "utilityName";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;
  totalFilterItems:any;

  selectedInput:string = "";
  columnField: any = {}

  modalService:any;

  public params = {
    apartmentId: this.sessionService.apartmentId
  }
  package: any = {};
  columnData: any;
  @ViewChild('gridResident', { static: false }) gridResident: jqxGridComponent;
  @ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
  waterReportDataList: any;

  blockId = null;
  blockNo: string = "";
  end_date = null;
  start_date = null;

  constructor(
    private lookupService: LookupService,
    private sharedService:SharedService,
    private apartmentService:ApartmentService,
    private utilityTrackerService:UtilityTrackerService,
    private sessionService:SessionService
  ) {

  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }
  getSelectedDeliveryType(event) {
    this.blockId = event[0].apartmentBlockId;

  //   if(this.selectedTower == null){
  //     this.filteredUtilityWaterListData= this.utilityWaterListData;
  //   }else{
  //     this.filteredUtilityWaterListData= this.utilityWaterListData.filter(x=> x.block == event[0].apartmentBlockNumber);
  //  }
  //  this.totalItems = this.filteredUtilityWaterListData.length;
    

   
   if(this.blockId != "" && this.blockId != null){
    let filterGroup = new jqx.filter();
    let filterOperator = 1;
    let filterValue = event[0].apartmentBlockNumber;
    let filterCondition = 'contains';
    let filterData = filterGroup.createfilter('stringfilter', filterValue, filterCondition);
    filterGroup.operator = 'or';
    filterGroup.addfilter(filterOperator, filterData);
    this.datagrid.showfiltercolumnbackground(false);
    this.columnData.forEach(item => {
      if(item.datafield === 'block') {
        this.datagrid.addfilter(item.datafield, filterGroup, true);
      }
    })
    this.datagrid.applyfilters();
  }
  else {
    this.datagrid.clearfilters();
  }
  

   
   }

   onSearchFilter(){
    if(this.utilitySearch != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.utilitySearch;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
   }

  onFilterByTowerChange(){
    this.selectedTower = event[0].apartmentBlockId;
    
    if(this.selectedTower == 'All'){
      this.filteredUtilityWaterListData= this.utilityWaterListData;
    }else{
      this.filteredUtilityWaterListData= this.utilityWaterListData.filter(x=> x.block == this.selectedTower);

      
   }
   this.totalItems = this.filteredUtilityWaterListData.length;

  

  }


  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  getUtilityFromPeriod(period_id){
    let electeicalListPeriods = this.utilityElecteicalListPeriods.find(x=> x.period_id == period_id);
    if(electeicalListPeriods){
      return  electeicalListPeriods.periods[0].periodFrom.slice(0,10)
    }
  }

  getUtilityToPeriod(period_id){
    let electeicalListPeriods = this.utilityElecteicalListPeriods.find(x=> x.period_id == period_id);
    if(electeicalListPeriods){
      return electeicalListPeriods.periods[0].periodTo.slice(0,10)
    }
  }



getAllWaterUtiliy(){
  this.selectedTower = '';
  this.isReportLoaded = false;

  	// jqx column generating
		var cellsrenderer = (row, column, value) => {
			return '<div class="jqx-custom-inner-cell">' + value + '</div>';
		}

		var columnrenderer = (value) => {
			return '<div style="padding: 14px">' + value + '</div>';
		}

		this.isReportLoaded = false;



			this.columnData = [
			{
				text: 'Block Number',
				datafield: 'block',
				minwidth: 100,
				cellsrenderer: cellsrenderer,
				renderer: columnrenderer
			},
			{
				text: 'Unit Number',
				datafield: 'aptUnit',
				minwidth: 100,
				cellsrenderer: cellsrenderer,
				renderer: columnrenderer
			},
			{
				text: 'Connection No',
				// datafield: 'waterCharge[0].connectionNo',
				width: 200,
				cellsrenderer:  (row, column, value) => {
          var content = this.waterReportDataList.records[row].waterCharge.length > 0 ? this.waterReportDataList.records[row].waterCharge[0]["connectionNo"] === null ? '' : this.waterReportDataList.records[row].waterCharge[0]["connectionNo"] : ''
          return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
        },
				renderer: columnrenderer
			},
			{
				text: 'Prev Reading',
				// datafield: 'waterCharge[0].prevReading',
				width: 200,
				cellsrenderer:  (row, column, value) => {
          var content = this.waterReportDataList.records[row].waterCharge.length > 0 ? this.waterReportDataList.records[row].waterCharge[0]["prevReading"] : ''
          return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
        },
				renderer: columnrenderer
			},
			{
				text: 'currReading',
				// datafield: 'currReading',
				width: 200,
				cellsrenderer:  (row, column, value) => {
          var content = this.waterReportDataList.records[row].waterCharge.length > 0 ? this.waterReportDataList.records[row].waterCharge[0]["currReading"] : ''
          return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
        },
				renderer: columnrenderer
			},


			];




  const params = {
    apartmentId: this.sessionService.apartmentId
  }
  this.utilityTrackerService.getUtilityTrackerWaterChargesByApartmentId(params).subscribe((res: any) => {
    let gridSourceData = {
        localdata: res,
        datatype: "array"
      }


    this.waterReportDataList = new jqx.dataAdapter(gridSourceData);

    this.isReportLoaded = true;
    this.showItems();


    this.utilityWaterListData = res;
    this.filteredUtilityWaterListData= this.utilityWaterListData;
    this.totalItems = this.filteredUtilityWaterListData.length;

    if (this.totalItems > this.itemLimit) {
      this.ItemEndIndex = this.itemLimit;
    }
    else {
      this.ItemEndIndex = this.totalItems;
    }

    this.isReportLoaded = true;

  });

}

showItems() {

  this.totalItems = this.waterReportDataList.length;

  if (this.totalItems > this.itemLimit) {
    this.ItemEndIndex = this.itemLimit;
  }
  else {
    this.ItemEndIndex = this.totalItems;
  }

}

onGlSearchFilter(event) {
  if(event != ""){

    let filtergroup = new jqx.filter();
    let filter_or_operator = 1;
    let filtervalue = event;
    let filtercondition = 'contains';
    let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
    filtergroup.operator = 'or';
    filtergroup.addfilter(filter_or_operator, filterData);
  
    this.datagrid.showfiltercolumnbackground(false);
  
    this.columnData.forEach(item => {
      if(item.datafield != 'Actions') {
      this.datagrid.addfilter(item.datafield, filtergroup, true);
      }
    })
  
    this.datagrid.applyfilters();
    
    }
    else {
    this.datagrid.clearfilters();
    }
   }

   getPrintParams(event){
    this.datagrid.exportdata(event, 'electricityReportData');
  }

    goBack() {
        this.matDrawer.close();
      }
    
	  filterApply() {
        this.goBack();
        this.getAllWaterUtiliy();
        
      }
    
      clearFilter() {
        this.blockId = null;
     this.start_date = null;
     this.end_date = null;
        // =this.getTicketByAdmin();
	
        this.getAllWaterUtiliy();
        this.goBack();
       
      }


  ngOnInit() {
    this.package.deliveryTypeId = '';




    let params = {
      apartmentId: this.sessionService.apartmentId
     }
      this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
        this.propertyTowerData = res;
    });
     this.getAllWaterUtiliy();

   }




  }
