import { Component, OnInit, Input } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-tower-reading-info',
  templateUrl: './tower-reading-info.component.html',
  styleUrls: ['./tower-reading-info.component.scss']
})
export class TowerReadingInfoComponent implements OnInit {

  @Input() tower:any;

  @Input() waterReadingPeriodID:any;

  @Input() allApartmentWaterCharge:any;

  @Input() utility_selected_category:any;

  isEdit:boolean = false;
  waterReading: any;
  isTowersLoaded:boolean = false;

  isUnitSold:boolean = false;
  isUnitVacant:boolean = false;

  towerUnitData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  public params = {
    apartmentBlockId: parseInt(this.tower.apartmentBlockId)
  }
  constructor(
  	private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private utilityTrackerService:UtilityTrackerService,
    private sharedService:SharedService,
    private sessionService:SessionService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }


  ngOnInit() {

    this.waterReading = {};

    let params = {
      apartmentBlockId: parseInt(this.tower.apartmentBlockId)
    }
    this.apartmentService.getApartmentBlockUnitByBlockId(params).subscribe((res: any) => {

      this.towerUnitData = res;
      this.totalItems = this.towerUnitData.length;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }
      for (let unit of this.towerUnitData) {
        let block_unit = this.allApartmentWaterCharge.find(x => x.aptUnit == unit.apartmentBlockUnitNumber)
        if (block_unit) {
          unit.water_charge = block_unit.waterCharge[0];
        } else {
          unit.water_charge = {};
        }
      }
      this.isTowersLoaded = true;

    });

  }

}
