import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MatDrawer } from '@angular/material/sidenav';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';

@Component({
  selector: 'app-utility-report-data',
  templateUrl: './utility-report-data.component.html',
  styleUrls: ['./utility-report-data.component.scss']
})
export class UtilityReportDataComponent implements OnInit {

  pageName: string = "";
	pageDesp: string = "";

	userReportDataList: any;

	userReportData: any;

	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	totalItems: any;
	itemLimit: any = 10;

	unitFieldType: string = "unitno";
	unitOrder: boolean = true;

	isDataLoaded: boolean = false;

	unitBlocksData: any;


	columnData: any;
	@ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
	reportData:any = "";
	@ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
	blockId = null;
    blockNo: string = "";
    end_date = null;
    start_date = null;

	constructor(
		private router: Router,
		private route: ActivatedRoute,
		private userService: UserService,
		private apartmentService: ApartmentService,
		private lookupService: LookupService,
		private sharedService: SharedService,
    private sessionService: SessionService,
    private utilityService: UtilityTrackerService
	) { }

	onGlSearchFilter(event) {
        if(event != ""){
      
          let filtergroup = new jqx.filter();
          let filter_or_operator = 1;
          let filtervalue = event;
          let filtercondition = 'contains';
          let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
          filtergroup.operator = 'or';
          filtergroup.addfilter(filter_or_operator, filterData);
        
          this.datagrid.showfiltercolumnbackground(false);
        
          this.columnData.forEach(item => {
            if(item.datafield != 'Actions') {
            this.datagrid.addfilter(item.datafield, filtergroup, true);
            }
          })
        
          this.datagrid.applyfilters();
          
          }
          else {
          this.datagrid.clearfilters();
          }
		 }
		 
		 filterApply() {
      this.goBack();

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.blockNo;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
    
      this.datagrid.showfiltercolumnbackground(false);
    
      this.columnData.forEach(item => {
        if(item.datafield == 'block') {
        this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
    
      this.datagrid.applyfilters();
      
			//this.isDataLoaded = false;
			// let params = {
			// 	ApartmentID : parseInt(this.sessionService.apartmentId),
			// 	ApartmentBlockID : parseInt(this.blockId),
			// 	StartDate : this.getDate(this.start_date),
			// 	EndDate : this.getDate(this.end_date)
			// }
			// this.getBlockDetails();
			
		  }
		
		  clearFilter() {
			this.blockId = null;
		 this.start_date = null;
     this.end_date = null;
     
     this.datagrid.clearfilters();
			// =this.getTicketByAdmin();
			// let params = {
			// 	ApartmentID : parseInt(this.sessionService.apartmentId),
			// 	ApartmentBlockID : parseInt(this.blockId),
			// 	StartDate : this.getDate(this.start_date),
			// 	EndDate : this.getDate(this.end_date)
			// }
		//	this.getBlockDetails();
			this.goBack();
		  }

		  goBack() {
			this.matDrawer.close();
		  }

		  getPrintParams(event){
			this.datagrid.exportdata(event, 'UtilityReportData');
		  }

		  getSelectedBlock(event) {
      this.blockId = event[0].apartmentBlockId;
      this.blockNo = event[0].apartmentBlockNumber;
		  }

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		// this.itemLimit = event.itemLimit;
	}

	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}

	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'asc' : 'desc';
		}
		else return '';
	}

	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}

	isItemsAvailable() {
		return this.totalItems > 0 ? true : false;
	}

	isNoItemsAvailable() {
		return this.totalItems == 0 ? true : false;
	}

	isListofElectricity() {
		return this.pageName == "List of Utility Bills - Common Electricity";
	}

  isListofWater() {
    return this.pageName == "List of Utility Bills - Water";
  }
	
	getDate(date) {
		return moment(date).format("MM-DD-YYYY");
	}

	getBlockDetails() {
		//jqx column generating
		var cellsrenderer = (row, column, value) => {
			return '<div class="jqx-custom-inner-cell">' + value + '</div>';
		}

		var columnrenderer = (value) => {
			return '<div style="padding: 14px">' + value + '</div>';
		}

		this.isDataLoaded = false;

		var details = {
			apartmentId: this.sessionService.apartmentId,
		}

		if (this.isListofElectricity()) {

      this.columnData = [
        {
          text: 'Block Number',
          datafield: 'block',
          minwidth: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
        },
        {
          text: 'Unit Number',
          datafield: 'aptUnit',
          minwidth: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
        },
        {
          text: 'Connection No',
          // datafield: 'waterCharge[0].connectionNo',
          width: 200,
          cellsrenderer:  (row, column, value) => {
            var content = this.userReportDataList.records[row].electCharge.length > 0 ? this.userReportDataList.records[row].electCharge[0]["connectionNo"] === null ? '' : this.userReportDataList.records[row].electCharge[0]["connectionNo"] : ''
            return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
          },
          renderer: columnrenderer
        },
        {
          text: 'Prev Reading',
          // datafield: 'waterCharge[0].prevReading',
          width: 200,
          cellsrenderer:  (row, column, value) => {
            var content = this.userReportDataList.records[row].electCharge.length > 0 ? this.userReportDataList.records[row].electCharge[0]["prevReading"] : ''
            return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
          },
          renderer: columnrenderer
        },
        {
          text: 'currReading',
          // datafield: 'currReading',
          width: 200,
          cellsrenderer:  (row, column, value) => {
            var content = this.userReportDataList.records[row].electCharge.length > 0 ? this.userReportDataList.records[row].electCharge[0]["currReading"] : ''
            return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
          },
          renderer: columnrenderer
        },

      ];
			this.utilityService.getUtilityTrackerElectricityChargesByApartmentId(details).subscribe((res: any) => {
				let gridSourceData = {
					localdata: res,
					datatype: "array"
				}

				this.userReportDataList = new jqx.dataAdapter(gridSourceData);
				this.totalItems =  this.userReportDataList.length;
				this.isDataLoaded = true;
				this.showItems();

			})

		}
		else if (this.isListofWater()) {
      this.columnData = [
        {
          text: 'Block Number',
          datafield: 'block',
          minwidth: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
        },
        {
          text: 'Unit Number',
          datafield: 'aptUnit',
          minwidth: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
        },
        {
          text: 'Connection No',
          // datafield: 'waterCharge[0].connectionNo',
          width: 200,
          cellsrenderer:  (row, column, value) => {
            var content = this.userReportDataList.records[row].waterCharge.length > 0 ? this.userReportDataList.records[row].waterCharge[0]["connectionNo"] === null ? '' : this.userReportDataList.records[row].waterCharge[0]["connectionNo"] : ''
            return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
          },
          renderer: columnrenderer
        },
        {
          text: 'Prev Reading',
          // datafield: 'waterCharge[0].prevReading',
          width: 200,
          cellsrenderer:  (row, column, value) => {
            var content = this.userReportDataList.records[row].waterCharge.length > 0 ? this.userReportDataList.records[row].waterCharge[0]["prevReading"] : ''
            return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
          },
          renderer: columnrenderer
        },
        {
          text: 'currReading',
          // datafield: 'currReading',
          width: 200,
          cellsrenderer:  (row, column, value) => {
            var content = this.userReportDataList.records[row].waterCharge.length > 0 ? this.userReportDataList.records[row].waterCharge[0]["currReading"] : ''
            return '<div class="jqx-custom-inner-cell">' +  content + '</div>';
          },
          renderer: columnrenderer
        },

      ];
			this.utilityService.getUtilityTrackerWaterChargesByApartmentId(details).subscribe((res: any) => {

				let gridSourceData = {
					localdata: res,
					datatype: "array"
				}

				this.userReportDataList = new jqx.dataAdapter(gridSourceData);
				this.totalItems =  this.userReportDataList.length;
				this.isDataLoaded = true;
				this.showItems();
			})

		}



	}

	showItems() {

		this.totalItems = this.userReportDataList.length;

		if (this.totalItems > this.itemLimit) {
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

	}

	ngOnInit() {
		this.pageName = this.route.params['value'].name;

		let unitBlockParams = {
			apartmentId: parseInt(this.sessionService.apartmentId)
		}

		this.apartmentService.getApartmentBlockByApartmentId(unitBlockParams).subscribe((res: any) => {
			this.unitBlocksData = res;
		});

		// let params = {
		// 	LookupTypeId: 87
		// }
		// this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {
		// 	var data = res.filter(item => {
		// 		return item.lookupValueId == this.route.params['value'].id
    //   })
    //   debugger;
		// 	this.pageName = data[0].lookupValueName.replace('/', '');
		// 	this.pageDesp = data[0].description;
		// });

		this.getBlockDetails();


	}

}
