import { Component, OnInit, Output,Input,  EventEmitter } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { UtilityTrackerService } from 'src/app/api/controllers/UtilityTracker';

import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-water-reading',
  templateUrl: './water-reading.component.html',
  styleUrls: ['./water-reading.component.scss']
})
export class WaterReadingComponent implements OnInit {

  @Output() onCancel = new EventEmitter();
  @Input() utility_selected_category:any;

  isDataLoaded:boolean = false;
  isSetPeriod:boolean = true;

  propertyTowerData:any;

  waterReadingFromDate: any
  waterReadingToDate: any
  allApartmentWaterCharge: any;

  waterReadingPeriodID: any;

  unitsLength:any;

  public params = {
    apartmentId: this.sessionService.apartmentId
  }
  constructor(
  	private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private utilityTrackerService:UtilityTrackerService,
    private sharedService:SharedService,
    private sessionService:SessionService
  ) { }

  cancel(){
    this.onCancel.emit(false)
  }

  submitReadingPeriodForm(form){
    this.isSetPeriod = false;
    let water_reading_period = {

      "apartmentId": this.sessionService.apartmentId,
      "periodFrom": this.waterReadingFromDate,
      "periodTo": this.waterReadingToDate,
      "isActive": true,
      "insertedBy": parseInt(this.sessionService.userId),
      "insertedOn": new Date().toISOString(),
      "updatedBy": 0,
      "updatedOn": null
    }

    let params = {
      utilityTrackerWaterChargePeriod: water_reading_period
    }

    this.utilityTrackerService.addUtilityTrackerWaterChargePeriod(params).subscribe((period_res:any) =>{
      this.waterReadingPeriodID = period_res.message
      this.isSetPeriod = true;
    })
  }

  ngOnInit() {
    this.waterReadingPeriodID = undefined;
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
  	this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
  		this.propertyTowerData = res;
  		this.isDataLoaded = true;

  	});

    this.utilityTrackerService.getUtilityTrackerWaterChargesByApartmentId(params).subscribe((res:any) => {
      this.allApartmentWaterCharge = res;
    });

  }

}
